$bg-color: #0a192f;
$light-color: #ccd6f6;
$green-color: #64ffda;

.navbar {
  background-color: $bg-color;
  display: flex;
  align-items: center;
  font-family: "Roboto Mono", monospace;
  padding: 3% 0 1.5%;
  width: 100%;
  position: fixed;
  z-index: 100;

  ul {
    display: flex;
    justify-content: flex-end;
    width: 100%;

    li {
      font-size: clamp(0.9rem, 1vw, 2rem);
      list-style: none;
      padding-right: 3%;

      a {
        color: $light-color;
        text-decoration: none;

        &:hover {
          color: $green-color;
        }
      }
    }
    .resume-btn {
      background-color: transparent;
      border: $green-color 1px solid;
      border-radius: 5px;
      color: $green-color;
      padding: 1.1vh 1.1vw;
      padding: 8px 17px;

      &:hover {
        background-color: rgb(100, 255, 218, 0.1);
      }
    }
  }
}

.toggle-button {
  position: absolute;
  background-color: $bg-color;
  border: none;
  cursor: pointer;
  top: 2.8rem;
  right: 15rem;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  width: 2.5rem;
  height: 30%;

  span {
    height: 3px;
    background-color: $green-color;
    border-radius: 10px;
  }
  .bar1 {
    width: 100%;
  }
  .bar2 {
    width: 80%;
  }
  .bar3 {
    width: 60%;
  }
}

@media only screen and (max-width: 1450px) {
  .nav-nav {
    display: flex;
    justify-content: center;
  }
}

@media only screen and (max-width: 850px) {
  .navbar {
    height: 5%;
  }

  .toggle-button {
    display: flex;
    top: 2rem;
    right: 2rem;
  }

  .nav-nav {
    background-color: $bg-color;
    display: none !important;
    flex-direction: column;
    align-items: center;
    padding-top: 40%;
    padding-bottom: 10%;
    width: 100%;

    li {
      padding-block: 1.5%;
    }

    .resume-btn {
      border: hidden !important;
      padding: 0;

      &:hover {
        background-color: $bg-color !important;
      }
    }
  }

  .active-nav {
    display: flex !important;
  }
}
